.feedback {
  background-color: $color-deep-sky-blue-2;
  padding: 176px 0 242px;
  position: relative;
  overflow: hidden;

  @include vp-1023 {
    padding: 50px 0 120px;
  }

  @include vp-767 {
    padding: 50px 0 85px;
  }

  &::before {
    content: "";
    width: 1400px;
    height: 950px;
    position: absolute;
    left: -532px;
    top: -408px;
    z-index: 0;
    background: rgba($color-black-peral-3, 0.5);
    transform: rotate(-40.9deg);

    @include vp-1023 {
      content: none;
    }
  }

  &__picture {
    @include vp-1023 {
      display: none;
    }
  }

  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 2;
  }

  &__block {
    max-width: 585px;
    margin-right: 15px;
    margin-top: -5px;

    @include vp-1023 {
      flex: 1;
      margin: 0;
      max-width: 100%;
    }
  }

  &__title {
    font-family: "Roboto Condensed", sans-serif;
    font-weight: 400;
    font-size: 36px;
    line-height: 40px;
    color: $color-default-white;
    margin: 0 0 15px;

    @include vp-1023 {
      margin: 0 0 13px;
    }

    @include vp-767 {
      font-size: 30px;
      margin: 0 0 10px;
    }
  }

  &__description {
    font-family: "PT Mono", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: $color-default-white;
    margin: 0 0 35px;

    @include vp-1023 {
      margin: 0 0 24px;
    }

    @include vp-767 {
      font-size: 14px;
      margin: 0 0 35px;
      line-height: 19px;
    }
  }
}
